import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountsTransactionsJourneyService } from '../accounts-transactions-journey-config.service';
declare const $localize: any;

@Component({
  selector: 'bb-accounts-manage-list',
  template: `
    <div class="px-0 pt-4 pb-5 flex-column-reverse align-items-start">
      <button (click)="onNavigateBack()" class="bb-heading-widget__button btn btn-link pl-0 ml-0 mb-3">
        <i aria-hidden="true" class="bb-icon bb-icon-arrow-back"></i>
        <span>{{ backToAccounts }}</span>
      </button>
      <bb-header-ui
        headingType="h1"
        headingClasses="bb-heading-widget__heading"
        [heading]="manageMyAccounts"
      ></bb-header-ui>
    </div>

    <bb-product-summary-manage-accounts-widget
      [showIcons]="config.showAccountIcons"
    ></bb-product-summary-manage-accounts-widget>
  `,
})
export class AccountsManageComponent {
  readonly manageMyAccounts = $localize`:Manage my accounts heading|manage my accounts heading@@accounts-transactions-journey.accounts-manage.heading.manageMyAccounts:Manage my Accounts`;
  readonly backToAccounts = $localize`:Back to my accounts button title|back to my accounts button title@@accounts-transactions-journey.accounts-manage.buttonTitle.backToAccounts:Back to my Accounts`;

  constructor(
    private readonly router: Router,
    private readonly route: ActivatedRoute,
    readonly config: AccountsTransactionsJourneyService,
  ) {}

  onNavigateBack() {
    this.router.navigate(['..'], { relativeTo: this.route });
  }
}
